<script>
  import Button from "./button.svelte";
  import { createEventDispatcher } from "svelte";

  export let bgColor;
  let dispatch = createEventDispatcher();
  let y;
  function showAbout() {
    dispatch("showAbout");
  }
  function showGroups() {
    dispatch("showGroups");
  }
  function showNews() {
    dispatch("showNews");
  }
</script>

<style>
  @import url("https://fonts.googleapis.com/css?family=Lato&display=swap");

  nav {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    padding: 0;
    z-index: 30;
  }
  nav .transparent {
    background: transparent;
  }
  nav > .container {
    display: grid;
    grid-template-columns: minmax(100px, auto) minmax(100px, auto);
    align-items: center;
  }

  a {
    background: none;
    padding: 1rem 2rem;
    border: 1px solid #ccc;
    border-radius: 5px;
    text-decoration: none;
    cursor: pointer;
    font-family: "Lato", sans-serif;
    font-size: 16px;
    color: #000000;
  }

  a:hover {
    background: rgb(233, 233, 233);
  }
  .brand > h1 {
    color: #fff;
  }
  .public {
    justify-self: end;
  }
  .blue {
    background: #5c03fd;
    transition: 1s;
  }
</style>

<svelte:window bind:scrollY={y} />

<nav class="transparent" class:blue={y > 250}>
  <div class="container">
    <div class="brand">
      <h1>Small social network</h1>
    </div>
    <div class="public">
      <Button address="nav-item" caption="About Project" on:click={showAbout} />
      <Button
        address="nav-item"
        caption="Kinder groups"
        on:click={showGroups} />
      <Button address="nav-item" caption="News" on:click={showNews} />
    </div>

  </div>
</nav>
