<script>
  export let caption = "";
  export let type = "button";
  export let address;
  export let disabled;
</script>

<style>
  button,
  a {
    font: inherit;
    border: 1px solid #007bff;
    background: #007bff;
    padding: 0.5rem 1rem;
    color: #fff;
    border-radius: 5px;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.26);
    cursor: pointer;
    text-decoration: none;
  }

  button:focus {
    outline: none;
  }

  button:hover,
  button:active,
  a:hover,
  a:active {
    background: #0062cc;
    border-color: #005cbf;
    box-shadow: 1px 1px 8px rgba(77, 51, 51, 0.26);
  }

  button:disabled,
  button:disabled:hover,
  button:disabled:active {
    background: #6c757d;
    border-color: #6c757d;
    color: #fff;
    box-shadow: none;
    cursor: not-allowed;
  }

  .success {
    background: #01a129;
    border-color: #01a129;
  }
  .nav-item {
    background: #d200f5;
    border: 1px solid #d200f5;
    border-radius: 30px;
    font-size: 1rem;
    font-weight: bold;
    color: #fff;
    padding: 10px 19px;
    box-shadow: none;
  }

  .slogan {
    background: #8101fb;
    border: 1px solid #fff;
    border-radius: 30px;
    font-size: 1.1rem;
    font-weight: 700;
    padding: 1rem 2rem;
  }

  .slogan:hover {
    background: #be64da;
    border: 1px solid #fff;
  }

  .price-btn {
    background-image: linear-gradient(to bottom right, #3e00f9, #4c0bf9);
    border: 1px solid #4c0bf9;
    border-radius: 30px;
    padding: 0.5rem 2rem;
    font-weight: 500;
    font-size: 1rem;
  }
  .price-btn:hover {
    background: none;
    color: #000000;
    border: 1px solid #4c0bf9;
  }
  .contacts {
    background-image: linear-gradient(to bottom right, #3e00f9, #4c0bf9);
    border: 1px solid #4c0bf9;
    border-radius: 30px;
    padding: 0.5rem 3rem;
    font-weight: 500;
    font-size: 1rem;
    margin: 1rem;
  }
  .contacts:hover {
    background: none;
    color: #000000;
    border: 1px solid #4c0bf9;
  }
  .edit {
    background: #dc3545;
    border: #dc3545;
    color: #fff;
    box-shadow: none;
  }
  .edit:hover {
    background: #c82333;
    border: #bd2130;

    color: #fff;

    box-shadow: none;
  }
  .nav-item:hover,
  .nav-item:active,
  a:hover,
  a:active {
    background: none;
    border: none;
    box-shadow: none;
    font-size: 1.3rem;
    transition: 100ms;
  }
</style>

<button class={address} {type} on:click {disabled}>{caption}</button>
