<script>
  import Nav from "./UI/Nav.svelte";
  import Footer from "./UI/Footer.svelte";
  import Rocket from "./UI/Rocket.svelte";
  import Stars from "./UI/Stars.svelte";
  import Slogan from "./UI/Slogan.svelte";
  import Children from "./components/Children.svelte";
  import LoginForm from "./components/LoginForm.svelte";
  import Services from "./components/Service/Services.svelte";
  import Features from "./components/Features/Features.svelte";
  import About from "./components/About/About.svelte";
  import Price from "./components/Price/Price.svelte";
  import Contacts from "./components/Contacts/Contacts.svelte";
  import Groups from "./components/Groups/Groups.svelte";
  import { scale, fade } from "svelte/transition";
  let loginForm = false;
  let showGroups = false;
  function showLogin() {
    loginForm = !loginForm;
  }
  function showProfile() {
    showGroups = true;
  }
</script>

<style>
  main {
    position: relative;
    width: 100%;
    height: 100vh;
    background-image: url(./background.jpg);
    background-size: cover;
    background-position: bottom;
    background-repeat: no-repeat;
  }
</style>

<Nav on:showGroups={showProfile} />

<main>

  <Rocket />
  <Stars />
  <Slogan on:showLoginForm={showLogin} on:showProfile={showProfile} />
  {#if loginForm}
    <LoginForm
      on:cancelLogin={() => {
        loginForm = false;
      }} />
  {/if}

  {#if showGroups}
    <Groups trasition:fade on:cancel={() => (showGroups = false)} />
  {/if}

</main>
<Services />
<Features />
<About />
<Price />

<Contacts />
<Footer />
