<script>
  import { createEventDispatcher } from "svelte";
  import { fade, fly } from "svelte/transition";

  let groups = [
    {
      name: "group 1"
    },
    {
      name: "group 2"
    },
    {
      name: "group 3 "
    },
    { name: "group 4" }
  ];

  let dispatch = createEventDispatcher();
</script>

<style>
  .background {
    position: fixed;
    top: 4rem;
    left: 0;
    background: transparent;
    width: 100%;
    height: 90vh;
    z-index: 25;
  }
  .gruop {
    position: fixed;
    top: 4rem;
    right: 0;
    z-index: 26;
    width: 50%;
    height: 90vh;
    background: linear-gradient(45deg, #d600f5, #5903fe);
    border-top-left-radius: 2rem;
    border-bottom-left-radius: 2rem;
  }

  .gruop h3 {
    color: #fff;
    font-size: 3rem;
    text-transform: uppercase;
    margin: 0;
    text-align: center;
  }
</style>

<div class="background" transition:fade on:click={() => dispatch('cancel')} />
<div class="gruop" transition:fly={{ x: 500 }}>
  <h1>Groups</h1>
  {#each groups as group (group.name)}
    <div class="group-item">
      <img src="" alt="" />

      <h3>{group.name}</h3>

    </div>
  {/each}
</div>
