<script>
  import { createEventDispatcher } from "svelte";
  import { scale } from "svelte/transition";
  import Button from "../UI/Button.svelte";
  import { isNotEmpty } from "../validation/validators.js";

  let username = "";
  let password = "";
  let usernameIsValid = false;
  let passwordIsValid = false;
  let usernameIsTouched = false;
  let passwordIsTouched = false;

  $: usernameIsValid = isNotEmpty(username);
  $: passwordIsValid = isNotEmpty(password);

  let dispatch = createEventDispatcher();
  function submitForm() {
    dispatch("login", {
      username,
      password
    });
  }
  function cancelLogin() {
    dispatch("cancelLogin");
  }
</script>

<style>
  .form {
    position: absolute;
    width: 500px;
    height: 500px;
    top: calc((100% - 500px) / 2);
    left: calc((100vw - 500px) / 2);
    border-radius: 2rem;
    background: rgb(136, 8, 187);
    display: grid;
    grid-template-columns: minmax(100px, auto);
    justify-content: center;
    align-items: center;
    box-shadow: 8px 8px 10px rgba(165, 63, 212, 0.74);
  }
  .form > #loginForm {
    display: grid;
    grid-template-rows: repeat(3, minmax(50px, auto));
    justify-self: center;
    align-self: center;
  }
  form > h3 {
    font-size: 3rem;
    text-transform: uppercase;
    font-family: "Lato", sans-serif;
    color: #fff;
  }
  form label {
    font-family: "Lato", sans-serif;
    font-size: 16px;
    margin-bottom: 0.5rem;
    color: #fff;
  }
  .form-control {
    display: inline-block;
    width: 300px;
  }
  #loginForm > .form-control > input {
    display: inline-block;
    width: 100%;
    border-radius: 1rem;
    outline: none;
  }
  .danger {
    color: red;
  }
</style>

<div class="form" on:submit|preventDefault transition:scale>
  <form action="" id="loginForm">

    <h3>Login form</h3>

    <div class="form-control">
      <label for="username">Username</label>
      <input
        type="text"
        name="username"
        bind:value={username}
        on:blur={() => {
          usernameIsTouched = true;
          console.log('touched');
        }} />
      {#if !usernameIsValid && usernameIsTouched}
        <span class="danger">Enter valid username</span>
      {/if}
    </div>
    <div class="form-control">
      <label for="password">Password</label>
      <input
        type="password"
        name="password"
        bind:value={password}
        on:blur={() => {
          passwordIsTouched = true;
          console.log('touched');
        }} />
      {#if !passwordIsValid && passwordIsTouched}
        <span class="danger">Enter valid password</span>
      {/if}
    </div>
    <div class="form-control">
      <Button
        type="submit"
        address="slogan"
        caption="Login"
        on:click={submitForm}
        disabled={!usernameIsValid || !passwordIsValid} />
      <Button
        address="slogan"
        type="button"
        caption="Cancel"
        on:click={cancelLogin} />
    </div>
  </form>
</div>
